@import '../../variables.scss';

#loader {
    position: fixed;
    top: 50%;
    left: 50%;
    bottom: 50%;
    right: 50%;
    border: 4px solid $color-white;
    border-top: 4px solid $color-dark-blue;
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
    animation: rotate 2s linear infinite;
    margin-top: -3rem;
    margin-left: 1rem;
    z-index: 500;

    @keyframes rotate {
        0% {
            transform: rotate(0deg);
        }
    
        100% {
            transform: rotate(360deg);
        }
    }

    @media (max-width: $width-window-xs) {
        margin-left: 0;
    }
}